html, body {
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 16px;
  min-height: 100vh;
  overflow-x: hidden;
  background: #fff;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased; }
  @media (max-width: 599px) {
    html, body {
      font-size: 4vmin; } }

* {
  box-sizing: border-box; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

a {
  text-decoration: none; }

h1, h3 {
  font-weight: normal; }

figure {
  padding: 0;
  margin: 0; }

.block {
  display: block; }

.wrp {
  margin: 0 auto;
  max-width: 1180px; }

.flex, form .row, .menu, .services-list, .our-clients ul {
  display: flex;
  justify-content: space-between; }

.field label:not([for="file"]), .contact label:not([for="file"]), .about p, .intro article, .intro article p, section.estimate .field select {
  font-size: 1.125em;
  font-family: 'Montserrat', sans-serif;
  color: #565d67; }

/*********************************************************************
*  =Image replacement
*********************************************************************/
/*********************************************************************
*  =Centered content
*********************************************************************/
/*********************************************************************
*  =No underline
*********************************************************************/
/*********************************************************************
*  =Ellipsis
*********************************************************************/
/*********************************************************************
*  =Pseudo Icon
*********************************************************************/
.field .custom-select:before, .hover:before, .hover:after {
  content: " ";
  position: absolute; }

/*********************************************************************
*  =Flex
*********************************************************************/
.flex, form .row, .menu, .services-list, .our-clients ul {
  display: flex;
  justify-content: space-between; }

